import React, { Component } from 'react';
import NewsItem from './NewsItem';
import Spinner from './Spinner';
import PropTypes from 'prop-types';

export class News extends Component {
  static propTypes = {
    country: PropTypes.string,
    pageSize: PropTypes.number,
    category: PropTypes.string,
  };

  static defaultProps = {
    country: 'us',
    pageSize: 8,
    category: 'science',
  };

  constructor() {
    super();
    this.state = {
      articles: [],
      loading: false,
      page: 1,
      totalResults: 0,
    };
  }

  async componentDidMount() {
    this.fetchArticles();
  }

  fetchArticles = async (page = 1) => {
    this.setState({ loading: true });
    const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=064f954ae2704e1084faae364942a3ba&page=${page}&pageSize=${this.props.pageSize}`;

    try {
      let response = await fetch(url);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      let parsedData = await response.json();
      this.setState({
        articles: parsedData.articles,
        totalResults: parsedData.totalResults,
        loading: false,
        page: page,
      });
    } catch (error) {
      console.error("Error fetching articles:", error);
      this.setState({ loading: false });
    }
  };

  handlePre = () => {
    const newPage = this.state.page - 1;
    if (newPage > 0) {
      this.fetchArticles(newPage);
    }
  };

  handleNext = () => {
    const newPage = this.state.page + 1;
    const maxPages = Math.ceil(this.state.totalResults / this.props.pageSize);
    if (newPage <= maxPages) {
      this.fetchArticles(newPage);
    }
  };

  render() {
  

    return (
      <div className="container my-3">
        <h1 className="text-center" style={{ margin: '40px 0px' }}>NewsMonkey - Top Headlines</h1>
        {this.state.loading && <Spinner />}
        <div className="row">
          {!this.state.loading && this.state.articles.map((e) => {
            return (
              <div className="col-md-4 col-sm-8" key={e.url}>
                <NewsItem
                  title={e.title ? e.title.slice(0, 45) : ''}
                  description={e.description ? e.description.slice(0, 88) : ''}
                  imageUrl={e.urlToImage}
                  newsUrl={e.url}
                  author={e.author}
                  date={e.publishedAt}
                  source={e.source.name}
                />
              </div>
            );
          })}
        </div>
        <div className="container d-flex justify-content-between">
          <button
            disabled={this.state.page <= 1}
            type="button"
            className="btn btn-dark"
            onClick={this.handlePre}
          >
            &#x2190; Previous
          </button>
          <button
            disabled={this.state.page >= Math.ceil(this.state.totalResults / this.props.pageSize)}
            type="button"
            className="btn btn-dark"
            onClick={this.handleNext}
          >
            Next &rarr;
          </button>
        </div>
      </div>
    );
  }
}

export default News;
